import { classToPlain, plainToClass } from 'class-transformer';
import { AuditFields } from './AuditFields';

export class SellProductInfo {
    id: string;
    name: string;
    description: string;
    logoUrl: string;
    auditTrail!: AuditFields;
    searchPhrase: string;
    creditPrice: number;
    cashPrice: number;

    constructor() {
        this.id = "";
        this.name = "";
        this.description = "";
        this.logoUrl = "";
        this.searchPhrase = "";
        this.creditPrice = 0;
        this.cashPrice = 0;

    }
    static converter = {
        toFirestore(expense: SellProductInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): SellProductInfo {
            const data = snapshot.data(options)!;
            return plainToClass(SellProductInfo, data);
        }
    };
}