import { Component, Prop, Vue } from "vue-property-decorator";

import store from '@/store';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';
import { UtilityService } from '@/services/UtilityService';
import { triggerThreadSafeValidation } from 'vee-validate/dist/types/components/common';
import SellProduct from '@/components/SellProduct/SellProduct.vue'
import { SellProductInfo } from '@/dto/SellProductInfo';
@Component({
  components: {
    SellProduct
  }
})
export default class SellProducts extends Vue {


  productSearchLoading = false;
  productList: SellProductInfo[] = [];
  loading = false;
  searchDisabled = false;
  queryString = "";
  searchLoading = false;
  headers: any = [
    { text: 'Date Added', value: 'auditTrail.createdOn', },
    { text: 'Sell Product Id', value: 'id' },
    { text: 'Product Name', value: 'name' },
    { text: 'Logo', value: 'logoUrl' },


    { text: 'Added By', value: 'auditTrail.createdBy.email' },
    { text: 'Updated By', value: 'auditTrail.updatedBy.email' },
    { text: 'Actions', value: 'actions', sortable: false },

  ]
  editedIndex = 0;
  editedItem: SellProductInfo = new SellProductInfo();
  dialog = false;


  async searchProducts() {
    console.log('query:' + this.queryString)
    if (this.queryString !== "") {
      await this.searchQueryString();
    } else {
      await this.getProducts();
    }
  }
  async getProducts() {
    try {
      this.productList = [];
      this.productSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('creditProducts', SellProductInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.productList.push(doc.data());
        });
        Vue.toasted.show('Search complete', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      console.log(error)
      Vue.toasted.show(error, { icon: 'face', type: 'error' });
    }
    finally {
      this.productSearchLoading = false;
    }

  }
  editItem(item: SellProductInfo) {
    this.editedIndex = this.productList.indexOf(item)
    this.editedItem = Object.assign({}, item)
    this.$store.commit('setCloseSeller', true)
  }



  async deleteItem(item: SellProductInfo) {
    try {
      const res = await Swal.fire({
        title: 'Are you sure?',
        text: "Cash/Credit Product will be deleted!!. You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!',

      });
      if (res.isConfirmed) {
        await FirestoreService.deleteDocument(`creditProducts`, item.id);
        const deleteIndex = this.productList.indexOf(item)

        this.productList.splice(deleteIndex, 1)
        Swal.fire(
          'Deleted!',
          'Cash/Credit Product has been deleted.',
          'success'
        )
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    }

  }
  async searchQueryString() {
    try {
      this.searchDisabled = true;
      this.searchLoading = true;
      this.productList = [];
      const snapshot = await FirestoreService.getdocsStartingWithKey('creditProducts', this.queryString, 'name', SellProductInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.productList.push(doc.data());
        });
        Vue.toasted.show('Found ' + this.productList.length + ' items', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }

    } catch (error) {
      Vue.toasted.show(error.message, { icon: 'face', type: 'error' });

    } finally {
      this.searchLoading = false;
      this.searchDisabled = false;
      this.queryString = ""
    };
  }
}
